import * as Yup from 'yup'

const FormValidation = Yup.object().shape({
  name: Yup.string().required(' !'),
  email: Yup.string()
    .email('    email!')
    .required(' !'),
  telephone: Yup.string()
    .required(' !')
    .matches(/(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){12,14}(\s*)?/, {
      message: '    !',
      excludeEmptyString: false,
    }),
})

export default FormValidation
